.t-wrapper{
    padding: 0 3rem 0 3rem;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 7rem;
    flex-direction: column;
    gap: 2rem;
    position: relative;

}
.t-heading{
    align-self: start;

}
.t-heading > *{
    font-size: 2rem;
    font-weight: bold;

}
.t-heading span:nth-child(2){
    color: var(--orange);

}

.t-blur1{
    left: 38rem;
    top:16rem;
}
.testimonial{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2rem;
    width: 35rem;
    height: 12rem;
    background:rgba(255, 255, 255, 0.26);
    border: 7px solid var(--boxShadow);
    border-radius: 20px;
}

.testimonial > img{
    width: 6rem;
    height: 6rem;
    border-radius: 100%;
    transform: scale(0.7);
}
.testimonial > span{
    color: var(--gray)

}

/* slider */
.t-wrapper .swiper{
    width: 100%;
    height: 70%;

}
.t-wrapper .swiper-slide{
    display: flex;
    align-items: center;
    justify-content: center;
}


.t-wrapper .swiper-pagination-bullet-active{
    background: var(--orange);
}